.wrapper {
}
.title {
  font-size: calc(22px + var(--device-wv));
}
.info {
  border: 1px solid var(--color-light-blue);
  border-radius: 2px;
  padding: 10px;
  font-size: 14px;
}
.infoItem {
  border-bottom: 1px solid var(--color-light-blue);
  padding: 5px;
}
.infoItemTitle {
  display: inline-block;
  color: var(--color-black-light);
  min-width: 100px;
}
.message {
  font-size: 12px;
  color: var(--color-red);
  text-align: center;
  margin-top: 10px;
}

.iconWikiData, .iconWiki, .iconGoogleMaps {
  position: relative;
  vertical-align: middle;
  margin: -5px 0;
}
.iconWikiData {
  font-size: 25px;
  top: -2px;
}
.iconWiki {
  font-size: 22px;
  top: -2px;
  left: 1px;
}
.iconGoogleMaps {
  font-size: 20px;
  top: -3px;
  left: 3px;
}
