.button {
  display: block;
  background-color: var(--color-white);
  box-shadow: inset 0 -1px 0 0 var(--color-blue-grey);
  border: 1px solid var(--color-dark-blue);
  border-radius: 4px;
  font-size: 15px;
  cursor: pointer;
  margin: 0 auto;
  padding: 10px 25px;
}
.button:hover {
  background-color: var(--color-light-blue);
}
